<template>
	<div>
		<!-- 面包屑导航区 -->
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
			<el-breadcrumb-item>用户管理</el-breadcrumb-item>
			<el-breadcrumb-item>用户列表</el-breadcrumb-item>
		</el-breadcrumb>
		
		<!-- 卡片视图区域 -->
		<el-card class="box-card">
			<!-- 搜索与添加区域 -->
				<el-row :gutter="20">
					<el-col :span="8">
						<!-- 搜索与添加 -->
						<el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
						  <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
						</el-input> 
					</el-col>
					<el-col :span="4">
						<el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
					</el-col>
				</el-row>
				
				<!-- 用户列表区域 -->
				<el-table :data="userlist" border stripe>
					<el-table-column type="index" label="#"></el-table-column>
					<el-table-column label="姓名" prop="username"></el-table-column>
					<el-table-column label="邮箱" prop="email"></el-table-column>
					<el-table-column label="电话" prop="mobile"></el-table-column>
					<el-table-column label="角色" prop="role_name"></el-table-column>
					<el-table-column label="状态">
						<template slot-scope="scope">
							<!-- {{scope.row}} 拿到当前这一行的数据-->
							<el-switch v-model="scope.row.mg_state" @change="userStateChanged(scope.row)"></el-switch>
						</template>
					</el-table-column>
					<el-table-column label="操作"  width="180px">
						<template slot-scope="scope">
							<el-tooltip effect="dark" content="修改" placement="top" :enterable="false" :open-delay="3000">
							      <!-- 修改按钮 -->
										<el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
							</el-tooltip>
							<el-tooltip effect="dark" content="删除" placement="top" :enterable="false" :open-delay="3000">
							      <!-- 删除按钮 -->
							      <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserId(scope.row.id)"></el-button>
							</el-tooltip>
							<el-tooltip effect="dark" content="角色" placement="top" :enterable="false" :open-delay="3000">
							      <!-- 分配角色按钮 -->
							      <el-button type="warning" icon="el-icon-setting" size="mini" @click="setRole(scope.row)"></el-button>
							</el-tooltip>
						</template>
						
					</el-table-column>
				</el-table>
				
				<!-- 分页区域 -->
				<el-pagination
				      @size-change="handleSizeChange"
				      @current-change="handleCurrentChange"
				      :current-page="queryInfo.pagenum"
				      :page-sizes="[1, 2, 5, 10]"
				      :page-size="queryInfo.pagesize"
				      layout="total, sizes, prev, pager, next, jumper"
				      :total="total">
				</el-pagination>
		</el-card>
		
		<!-- 添加用户的对话框 -->
		<el-dialog title="添加用户" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
			<!-- 内容主体区域 -->
		  <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
		    <el-form-item label="用户名" prop="username">
		      <el-input v-model="addForm.username"></el-input>
		    </el-form-item>
				<el-form-item label="密码" prop="password">
				  <el-input v-model="addForm.password"></el-input>
				</el-form-item>
				<el-form-item label="邮箱" prop="email">
				  <el-input v-model="addForm.email"></el-input>
				</el-form-item>
				<el-form-item label="手机" prop="mobile">
				  <el-input v-model="addForm.mobile"></el-input>
				</el-form-item>
			</el-form>
			<!-- 底部区域 -->
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="addDialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="addUser">确 定</el-button>
		  </span>
		</el-dialog>
		
		<!-- 修改用户的对话框 -->
		<el-dialog title="修改用户" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
		  <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
				
		    <el-form-item label="用户名" prop="username">
		      <el-input v-model="editForm.username" disabled></el-input>
		    </el-form-item>
				<el-form-item label="邮箱" prop="email" >
				  <el-input v-model="editForm.email"></el-input>
				</el-form-item>
				<el-form-item label="手机号" prop="mobile" >
				  <el-input v-model="editForm.mobile"></el-input>
				</el-form-item>
		  
		  </el-form>
			
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="editDialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="editUserInfo">确 定</el-button>
		  </span>
		</el-dialog>
		
		<!-- 分配角色的对话框 -->
		<el-dialog title="分配角色" :visible.sync="setRoleDialogVisible" width="50%" @close="setRoleDialogClosed">
		  <div>
				<p>当前用户：{{userInfo.username}}</p>
				<p>当前角色：{{userInfo.role_name}}</p>
				<p>
					<el-select v-model="selectedRoleId" placeholder="请选择">
						<el-option
						      v-for="item in rolesList"
						      :key="item.id"
						      :label="item.roleName"
						      :value="item.id">
						    </el-option>
					</el-select>
				</p>
			</div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="setRoleDialogVisible = false">取 消</el-button>
		    <el-button type="primary" @click="saveRoleInfo">确 定</el-button>
		  </span>
		</el-dialog>
		
	</div>
</template>

<script>
	export default {
		data(){
			
    var checkEmail = (rule, value, cb) => {
      // 验证邮件的正则表达式
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        // 合法的邮箱
        return cb()
      }
      cb(new Error('请输入一个合法的邮箱地址'))
    }

    var checkMobile = (rule, value, cb) => {
      // 验证邮件的正则表达式
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        // 合法的邮箱
        return cb()
      }
      cb(new Error('请输入一个合法的手机号码'))
    }
		
		
			return {
				//获取用户列表的参数
				queryInfo:{
					query:'',
					pagenum:1,//当前页
					pagesize:2,//默认每页显示多少条
				},
				userlist:[],
				total:0,
				addDialogVisible:false,//控制添加用户对话框的显示和隐藏
				addForm:{//添加用户的表单数据
					username:'',
					password:'',
					email:'',
					mobile:''
				},
				addFormRules:{//添加表单验证规则对象
					username:[
						{ required: true, message: '请输入用户名', trigger: 'blur' },
						{min:3,max:10,message:'用户名的长度在3~10个字符之间',trigger:'blur'}
					],
					password:[
						{ required: true, message: '请输入密码', trigger: 'blur' },
						{min:6,max:15,message:'用户名的长度在6~15个字符之间',trigger:'blur'}
					],
					email:[
						{ required: true, message: '请输入邮箱', trigger: 'blur' },
						{ validator: checkEmail, trigger: 'blur' }
					],
					mobile:[
						{ required: true, message: '请输入手机号', trigger: 'blur' },
						{ validator: checkMobile, trigger: 'blur' }
					]
				},
				editFormRules:{//修改表单的验证规则
					email:[
						{ required: true, message: '请输入邮箱', trigger: 'blur' },
						{ validator: checkEmail, trigger: 'blur' }
					],
					mobile:[
						{ required: true, message: '请输入手机号', trigger: 'blur' },
						{ validator: checkMobile, trigger: 'blur' }
					],
				},
				editDialogVisible:false,//控制修改用户对话框的显示与隐藏
				editForm:{},//查询到到用户信息对象
				setRoleDialogVisible:false,//控制分配角色对话框的显示与隐藏
				userInfo:{},//需要被分配角色的用户信息
				rolesList:[],//所有角色的数据列表
				selectedRoleId:{},//已选中的角色id值
			}
		},
		methods:{
			async getUserList(){
			const {data:res} = await this.$http.get('users',{ params: this.queryInfo })
			if (res.meta.status !== 200) return this.$message.error('获取用户列表失败')
			this.userlist = res.data.users
			this.total = res.data.total
			console.log(res);
			},
			
			handleSizeChange(newSize) {//监听 pagesize 改变事件
			        console.log(`每页 ${newSize} 条`);
							this.queryInfo.pagesize = newSize
							this.getUserList()
			      },
			
			handleCurrentChange(newPage) {//监听 页码值 改变事件
			        console.log(`当前页: ${newPage}`);
							this.queryInfo.pagenum = newPage
							this.getUserList()
			      },
			async	userStateChanged(userinfo){//监听switch开关状态的改变
							console.log(userinfo)
						const {data : res}	= await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
						if(res.meta.status !== 200) {
							userinfo.mg_state = !userinfo.mg_state
							return this.$message.error('更新用户状态失败！')
						}
						this.$message.success('更新用户状态成功！')
						},
			addDialogClosed(){
							this.$refs.addFormRef.resetFields();
						},
			addUser(){//点击按钮添加用户
							this.$refs.addFormRef.validate(async valid => {
								if(!valid) return //如果预校验不通过不进行请求
								//通过可以发起添加用户的网络请求
								const {data : res} = await this.$http.post('users', this.addForm)
								if(res.meta.status !== 201){
									this.$message.error('添加用户失败！')
								}
								this.$message.success('添加用户成功！')
								//添加成功后关闭对话框
								this.addDialogVisible = false
								//添加成功后重新刷新列表
								this.getUserList()
							})
			},
			async showEditDialog(id){//展示用户编辑的对话框
				const {data:res} = await this.$http.get('users/' + id)
				if(res.meta.status !== 200){
					return this.$message.error('查询用户信息失败')
				}
				this.editForm = res.data
				this.editDialogVisible = true
			},
			editDialogClosed(){//监听修改用户对话框的关闭事件
				this.$refs.editFormRef.resetFields()
			},
			editUserInfo(){//修改用户信息并提交
				this.$refs.editFormRef.validate(async valid => {
					if(!valid) return //如果预校验不通过不进行请求
					//通过可以发起添加用户的网络请求
					const {data : res} = await this.$http.put('users/' + this.editForm.id,
					{email:this.editForm.email,mobile:this.editForm.mobile})
					if(res.meta.status !== 200){
							return this.$message.error('更新用户信息失败！')
					}
					
					//修改成功后关闭对话框
					this.editDialogVisible = false
					//修改成功后重新刷新列表
					this.getUserList()
					//提示修改成功
					this.$message.success('更新用户信息成功！')
				})
			},
			async removeUserId(id){//根据id删除对应的用户信息
				// console.log(id)
				//弹框询问
			const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
				          confirmButtonText: '确定',//如果用户确认删除 则返回值为字符串 confirm
				          cancelButtonText: '取消',//如果用户取消删除 则返回值为字符串 cancel
				          type: 'warning'
				        }).catch(err => err)
								
								 // console.log(confirmResult)
									if(confirmResult !== 'confirm'){
										return this.$message.info('已取消删除')
									}
									const {data : res} = await this.$http.delete('users/' + id)
									if(res.meta.status !== 200){
										return this.$message.error('删除用户失败')
									}
									this.$message.success('删除用户成功')
									//删除成功后重新刷新列表
									this.getUserList()
								
			},
			async setRole(userInfo){//展示分配角色的对话框
			
			this.userInfo = userInfo
			
			//在展示对话框之前，获取所有角色的列表
			const {data : res} = await this.$http.get('roles')
			
			if(res.meta.status !== 200){
				return this.$message.error('获取角色列表失败！')
			}
			this.rolesList = res.data
	
			this.setRoleDialogVisible = true
			},
			
			async saveRoleInfo(){//点击按钮分配角色
				if(!this.selectedRoleId){
					return this.$message.error('请选择要分配的角色')
				}
				const {data : res} = await this.$http.put(`users/${this.userInfo.id}/role`,{rid:this.selectedRoleId})
				if(res.meta.status !== 200){
					return this.$message.error('更新角色失败！')
				}
				this.$message.success('更新角色成功！')
				this.getUserList()
				this.setRoleDialogVisible = false
			},
			setRoleDialogClosed(){//监听分配角色对话框的关闭事件
				this.selectedRoleId = ''
				this.userInfo = {}
				
			}
			
		},
		
		created(){
				this.getUserList()
			},
	}
</script>

<style lang="less" scoped>

</style>
